.header img {
  height: 40px;
}

.header {
  background-color: #5692d7;
  height: 50px;
  display: flex;
  position: fixed;
  width: 100%;
  top: 0;
}

.loginBlock a {
  color: #5692d7;
}


.login {
  color: #fff;
  margin: 0 10px;
}

.wrapper {
  display: flex;
  align-items: center;
  height: 50px;
  flex-flow: row nowrap;
}

.buttons {
  height: 30px;
  background-color: #fff;
  border: none;
  border-radius: 3px;
  color: #5692d7;
  width: 100px;
  transition: .3s all;
}

.buttons:hover {
  background-color: #f0f0f0;
  cursor: pointer;
}

.buttons:active {
  background-color: cornflowerblue;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}
