.nav {
    font-size: 15px;
    background-color: #faf9f5;
    padding: 20px 0;
    width: 200px;
    min-width: 200px;
    position: sticky;
    top: 0;
}

.item a {
    color: black;
    text-decoration: none;
    padding: 12px;
    font-size: 17px;
    display: block;
}

.item a:hover {
    background-color: #f5f5f5;
}

.item a.active {
    color: blue;
}

.logo {
    height: 17px;
    width: 17px;
    margin: 0 10px;
}
.linksModule{
    position: relative;
}